.page1bg {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}
.Page1Content {
	width: 50vw;
	height: 60vh;
	background: linear-gradient(184.26deg, #610e91 3.47%, #120352 96.53%);
	box-shadow: 3px 1px 38px -12px #000000;
	border-radius: 28px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
	opacity: 0;
	transform: scale(0);
	transition: all 500ms;
	transition: all 500ms;
}
.fadeIn {
	opacity: 1;
	transform: scale(1);
	transition: all 500ms;
	transition: all 500ms;
}
.fadeOut {
	opacity: 0;
	transform: scale(0);
	transition: all 500ms;
	transition: all 500ms;
}
.left,
.right {
	width: 50%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.equinoxlogo {
	width: 80%;
	/* height: 245px; */
	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.dates {
	font-family: 'Uni Sans Demo';
	font-style: normal;
	font-weight: normal;
	font-size: 2vw;
	line-height: 19px;
	text-align: right;
	margin-top: 20%;
	color: #ffffff;
}
.timer {
	font-family: 'Uni Sans Demo';
	font-style: normal;
	font-weight: normal;
	font-size: 4vw;
	line-height: 42px;
	text-align: right;
	color: #ffffff;
	margin-top: 0;
	margin-bottom: 10%;
}

#devfolio-apply-now {
	align-items: center;
	outline: none;
	background-color: #3770ff;
	border-radius: 3px;
	border: none;
	color: #ffffff;
	cursor: pointer;
	display: flex;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
		sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
	font-size: 18px;
	font-weight: 600;
	padding: 12px 20px;
	text-align: center;
	text-transform: lowercase;
	cursor: pointer;
}

#devfolio-apply-now:not([disabled]) {
	cursor: pointer;
}

#devfolio-apply-now:hover {
	background-color: #2954bf !important;
}
.links {
	display: flex;
	margin-top: 5%;
	width: 48%;
	align-items: center;
	justify-content: space-between;
}
.linklogos {
	margin: 2%;
	width: 16%;
	cursor: pointer;
}
.at {
	width: 13%;
}
.linklogos:hover {
	transform: scale(1.1);
	transition: all 0.5s;
}
.contact {
	width: 100%;
	opacity: 0;
	font-family: 'roboto';
	font-style: normal;
	color: white;
	transition: all 0.3s;
	text-align: center;
}
.showContact {
	width: 100%;
	height: auto;
	opacity: 1;
	text-align: center;
	transition: all 0.3s;
}

@media screen and (max-width: 1030px) {
	.Page1Content {
		width: 80vw;
		height: 80vh;
		flex-direction: column;
		opacity: 1;
	}
}

@media screen and (max-width: 720px) {
	.Page1Content {
		width: 80vw;
		height: 80vh;
		flex-direction: column;
		opacity: 1;
	}
	.equinoxlogo {
		width: 65%;
	}
	.left,
	.right {
		width: 100%;
		height: 50%;
	}
	.left {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.dates {
		font-size: 6vw;
	}
	.timer {
		font-size: 9vw;
		margin-bottom: 2%;
	}
	.linklogos {
		width: 16%;
	}
	.at {
		width: 14%;
	}
	.links {
		width: 70%;
	}
	.dates {
		margin-top: 5%;
	}
}

