	display: flex;
	flex-direction: column;
	text-align: center;
	justify-content: space-evenly;
}
.rightfaq {
}
.faq {
	width: 90%;
	height: 10vh;

	background: rgba(0, 141, 200, 0.45);
	box-shadow: 4px 0px 20px -7px rgba(0, 115, 199, 0.46);
	border-radius: 28px;
	-webkit-backdrop-filter: blur(15px);
	        backdrop-filter: blur(15px);
	text-align: center;
	cursor: pointer;
	position: relative;
	z-index: 1000000;
	transition: all 0.3s;
}

.question {
	height: 100%;
	width: 90%;
	margin: auto;
	overflow: hidden;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'acumin-pro';
	font-style: normal;
	font-weight: bold;
	font-size: 2vw;
	text-align: center;

	color: #ffffff;
}
.answer {
	height: 0%;
	opacity: 0;
	width: 90%;
	margin: auto;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'acumin-pro';
	font-style: normal;
	font-weight: bold;
	font-size: 1vw;
	text-align: right;
	text-align: justify;

	color: #ffffff;
}
.show {
	height: 30vh;
	transition: all 0.3s;
}
.show .question {
	height: 25%;
	transition: all 0.3s;
}
.show .answer {
	height: 75%;
	opacity: 1;
	transition: all 0.3s;
}
